import React, {FC} from 'react';
import './style.scss'
import {observer} from "mobx-react-lite";

type TodoProps = {
    complete: boolean
    title: string
    id: number
    completedTodo: (id: number) => void
    handleDeleteTask: (id: number) => void
}

const Todo: FC<TodoProps> = observer(({title, id, complete, completedTodo, handleDeleteTask}) => {
    return (<div className={"todo"}>
        {/*<input type="checkbox" defaultChecked={complete} onChange={() => completedTodo(id)}/>*/}
        <div onClick={() => completedTodo(id)} className={`check ${complete ? "complete": ""}`}></div>
        <p className={"textTodo"}>{title}</p>
        <button className={"buttonDelete"} onClick={() => handleDeleteTask(id)}>delete</button>
    </div>)
})

export default Todo;
