import {makeAutoObservable} from "mobx";


class Todo {
    todos = []

    constructor() {
        makeAutoObservable(this)
    }

    addTodo(todo) {
        let todos = this.getTodos()
        todos.push(todo)
        this.todos = todos
        localStorage.setItem("tasks", JSON.stringify(todos))
    }

    removeTodo(id) {
        let todos = this.getTodos().filter(t => t.id !== id);
        this.todos = todos
        localStorage.setItem("tasks", JSON.stringify(todos))
    }

    completedTodo(id) {
        let todos = this.getTodos()

        todos.forEach(todo => {
            if (todo.id === id) {
                todo.complete = !todo.complete;
            }
        });

        this.todos = todos

        localStorage.setItem("tasks", JSON.stringify(todos))
    }

    getSorterTodos(sorter) {
        if (sorter === 'completed') {
            return [...this.todos.sort((prevTodo, nextTodo) => nextTodo.complete - prevTodo.complete)];
        }

        if (sorter === 'notCompleted') {
            return [...this.todos.sort((prevTodo, nextTodo) => prevTodo.complete - nextTodo.complete)];
        }

        if (sorter === '') {

            return this.getTodos();
        }

        return [...this.todos];
    }

    getTodos() {
        // fetch('https://jsonplaceholder.typicode.com/todos')
        //     .then(response => response.json())
        //     .then(json => this.todos = json)
        this.todos = JSON.parse(localStorage.getItem("tasks")) || []
        return this.todos;
    }

}

let todo = new Todo()

export default todo